* {
    font-family: Monaco, sans-serif;
  }
  
  .App {
    margin-top: 20px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
 
  
  .childContainer {
    margin-top: 6px;
    background-color: #d3d3d3e0;
    display: flex;
    flex-direction: column;
    padding: 5px 10px;
    width: 300px;
    cursor: pointer;
    border-radius: 5px;
  }
  .exportBtn{
    margin-top: 50px;
  }
  .childContainer:hover {
    background-color: #d3d3d3bf;
  }
  
  .inputContainer {
    display: flex;
    align-items: baseline;
    gap: 5px;
  }
  
  .inputContainer > span {
    margin-top: 5px;
  }
  
  .inputContainer__input {
    margin: 6px 0 0px 0;
    padding: 5px;
    display: flex;
    border: 1px solid lightgray;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    border-radius: 5px;
    background-color: #e7e7e7;
  }
  
  .first_input {
    margin: 0;
  }
  
  .childContainer > span {
    margin: 0 5px;
  }
  
  .reply {
    font-size: 12px;
    padding: 5px;
    border-radius: 5px;
    color: #4e4e4e;
    font-weight: 600;
    cursor: pointer;
  }
  
  .child {
    color: #ffffff;
    background-color: #569dff;
    letter-spacing: 0.8px;
  }